body {
  font-size: 14px;
  font-family: var(--font-family-base);
  font-weight: 400;
  line-height: 1.28;
  color: var(--color-fonts-gray);
  background-color: var(--color-light);

  @include mobile {
    padding-bottom: 95px; //место для .mobile-menu 
  }
}

a,
button,
input,
textarea,
svg * {
  transition-duration: var(--transition-duration);
}

a {
  text-decoration: none;
  color: inherit;
}

h1, h2, h3, h4 {
  font-weight: 500;
}
