:root {
  --color-light: #FFFFFF;
  --color-green: #46A358;
  --color-light-green: rgba(70, 163, 88, 0.2);
  --color-light-gray: #FBFBFB;
  --color-bg-gray: #F9F9F9;
  --color-fonts-dark: #3d3d3d;
  --color-fonts-gray: #727272;
  --color-border-button: #e5e5e5;
  --border: 1px solid var(--color-green);
  --border-radius: 6px;
  --shadow: 0 5px 0 0 var(--color-dark);
  --font-family-base: "Cera Pro", sans-serif;
  --container-width: 1200px;
  --section-padding-y: 70px;
  --button-height-small: 35px;
  --button-height-large: 40px;
  --transition-duration: 0.2s;
}

.container {
  max-width: var(--container-width);
  margin-inline: auto;
}

.visually-hidden {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  border: 0 !important;
  padding: 0 !important;
  white-space: nowrap !important;
  clip-path: inset(100%) !important;
  clip: rect(0 0 0 0) !important;
  overflow: hidden !important;
}

@media (max-width: 767px) {
  .hidden-mobile {
    display: none !important;
  }
}

@media (max-width: 560px) {
  .hidden-mobile-s {
    display: none !important;
  }
}

@media (min-width: 768px) {
  .visible-mobile {
    display: none !important;
  }
}

.accent-color {
  color: var(--color-green);
}

body {
  font-size: 14px;
  font-family: var(--font-family-base);
  font-weight: 400;
  line-height: 1.28;
  color: var(--color-fonts-gray);
  background-color: var(--color-light);
}
@media (max-width: 767px) {
  body {
    padding-bottom: 95px;
  }
}

a,
button,
input,
textarea,
svg * {
  transition-duration: var(--transition-duration);
}

a {
  text-decoration: none;
  color: inherit;
}

h1, h2, h3, h4 {
  font-weight: 500;
}

.button {
  border: none;
  border-radius: var(--border-radius);
  background-color: var(--color-green);
  color: var(--color-light);
  min-height: 35px;
}

.header {
  padding-top: 25px;
  margin-inline: clamp(20px, 4vw, 40px);
}
.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 53px;
  border-bottom: 1px solid var(--color-light-green);
}
@media (max-width: 560px) {
  .header__inner {
    display: none;
  }
}
.header__menu {
  z-index: 1;
  margin: 0 auto;
  font-size: 16px;
}
.header__menu-list {
  display: flex;
  align-items: center;
  column-gap: 50px;
}
@media (max-width: 1023px) {
  .header__menu-list {
    column-gap: 30px;
  }
}
.header__menu-item--active {
  color: var(--color-green);
  font-weight: 700;
}
.header__menu-link {
  position: relative;
  display: flex;
  white-space: nowrap;
  align-items: center;
  height: 48px;
  color: var(--color-fonts-dark);
}
@media (hover: hover) {
  .header__menu-link:hover::after {
    width: 100%;
  }
}
@media (hover: none) {
  .header__menu-link:active::after {
    width: 100%;
  }
}
.header__menu-link::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  translate: -50%;
  width: 0;
  height: 2px;
  background-color: var(--color-green);
  transition-duration: inherit;
}
.header__tools {
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 1;
  column-gap: 30px;
}
.header__tools:hover {
  cursor: pointer;
}
@media (max-width: 1023px) {
  .header__tools {
    margin-left: 30px;
  }
}
.header__icon-search {
  aspect-ratio: 1;
  width: 20px;
}
.header__icon-basket {
  height: 24px;
  width: 29px;
}
.header__button {
  display: inline-flex;
  align-items: center;
  position: relative;
  width: 100px;
  height: var(--button-height-small);
  padding-left: 41px;
  font-size: 16px;
  font-weight: 500;
}
@media (max-width: 1023px) {
  .header__button {
    font-size: 14px;
  }
}
.header__button::before {
  content: "";
  position: absolute;
  left: 17px;
  height: 20px;
  width: 20px;
  background-image: url("/img/icons/login-icon.svg");
  color: var(--color-light);
}
.header__burger-button {
  display: inline-flex;
  height: var(--button-height-small);
  padding-inline: 10px;
  font-weight: 500;
  font-style: 14px;
  align-items: center;
}
@media (min-width: 768px) {
  .header__burger-button {
    display: none;
  }
}
.header__search-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 561px) {
  .header__search-wrapper {
    display: none;
  }
}
.header__input-wrapper {
  position: relative;
  display: flex;
  flex-direction: row;
  width: 100%;
}
.header__input {
  position: relative;
  flex: 1;
  height: 45px;
  border: none;
  border-radius: 10px;
  background-color: var(--color-light-gray);
  margin-right: 8px;
}
.header__input:not(:active) {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.header__input-icon {
  background-color: var(--color-light-gray);
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}
.header__input-icon:active {
  display: none;
}
.header__button-filter {
  width: 45px;
  aspect-ratio: 1;
  padding: 12px;
  border-radius: 14px;
  box-shadow: 0 10px 20px 0 rgba(70, 163, 88, 0.3);
  background: linear-gradient(137deg, #81c78f 0%, #46a358 100%);
}
.header__button-filter-icon {
  display: flex;
  justify-content: center;
  align-items: center;
}

.banner {
  padding-top: 12px;
  margin-inline: clamp(20px, 4vw, 40px);
  margin-bottom: clamp(19px, 3.2vw, 46px);
}
.banner__body {
  display: grid;
  grid-template-columns: 1fr auto;
  background-color: var(--color-bg-gray);
}
@media (max-width: 1023px) {
  .banner__body {
    background-color: var(--color-light-green);
    border-radius: 30px;
  }
}
.banner__text-wrapper {
  display: flex;
  flex-direction: column;
  flex: 1;
  max-height: 450px;
  min-height: 190px;
  padding-top: clamp(24px, 4.72vw, 68px);
  padding-left: 40px;
}
@media (max-width: 1023px) {
  .banner__text-wrapper {
    padding-left: 23px;
  }
}
.banner__subtitle {
  font-weight: 500;
  font-size: clamp(11px, 2.65vw, 14px);
  line-height: 1.4;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: var(--color-fonts-dark);
  text-wrap: nowrap;
}
.banner__title {
  font-size: 70px;
  font-size: clamp(24px, 5.8vw, 70px);
  font-weight: 900;
  text-transform: uppercase;
  line-height: 1;
  color: var(--color-fonts-dark);
  margin-bottom: 5px;
  text-wrap: nowrap;
}
.banner__descrtiption {
  display: inline-block;
  max-width: 557px;
  max-height: 45px;
  font-weight: 400;
  font-size: clamp(12px, 2.65vw, 14px);
  line-height: 1.7;
  color: var(--color-fonts-gray);
  overflow: hidden;
}
.banner__button {
  position: relative;
  height: var(--button-height-large);
  width: max-content;
  padding-inline: 27px;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.25;
  margin-top: 44px;
}
@media (max-width: 1023px) {
  .banner__button {
    height: clamp(14px, 2.7vw, var(--button-height-large));
    font-size: 14px;
    background-color: transparent;
    color: var(--color-green);
    padding: 0;
    margin-top: 11px;
  }
  .banner__button::after {
    content: "";
    position: absolute;
    left: 80px;
    top: 7px;
    height: 20px;
    width: 20px;
    background-image: url("/img/icons/arrow-right-icon.svg");
    background-size: cover;
  }
}
.banner__image-wrapper {
  max-height: 450px;
  min-height: 190px;
  position: relative;
}
.banner__image-wrapper::before {
  content: " ";
  position: absolute;
  top: 295px;
  left: 70px;
  width: clamp(82px, 9.3vw, 135px);
  aspect-ratio: 1;
  background-image: url("/img/content/flowers-banner.png");
  background-size: cover;
}
@media (max-width: 1440px) {
  .banner__image-wrapper::before {
    display: none;
  }
}
@media (max-width: 560px) {
  .banner__image-wrapper::before {
    display: block;
    top: 110px;
    left: -8px;
  }
}
.banner__image {
  width: clamp(213px, 35vw, 518px);
  position: relative;
  top: -50px;
  left: 15px;
}
@media (max-width: 1023px) {
  .banner__image {
    top: -30px;
  }
}
@media (max-width: 767px) {
  .banner__image {
    top: -20px;
    overflow: hidden;
  }
}
@media (max-width: 560px) {
  .banner__image {
    top: -10px;
    left: -5px;
  }
}

