  display: flex;
  white-space: nowrap;
  align-items: center;
  height: 25px;
  color: var(--color-fonts-dark);
}
@media (hover: hover) {
  .products__nav-item:hover::after {
    width: 100%;
  }
}
@media (hover: none) {
  .products__nav-item:active::after {
    width: 100%;
  }
}
.products__nav-item::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
